export default "div {\n    box-sizing: border-box;\n}\n\n.outside {\n    position: absolute;\n    top: 0;\n    right: 0;\n    left: 0;\n    bottom: 0;\n    display: flex;\n    flex-direction: row;\n}\n#carousel-container {\n    top: 0;\n    right: 0;\n    left: 0;\n    bottom: 0;\n    display: block;\n    position: absolute;\n    display: block;\n    overflow: hidden;\n}\n.arrow {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    width: 30px;\n    opacity: 100%;\n    z-index: 1;\n}\n\n#left-arrow {\n    left: 0;\n}\n#right-arrow {\n    right: 0;\n}\n.nav-btn {\n    background: gray;\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    text-align: center;\n    font-size: 30px;\n    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);\n    color: white;\n    opacity: 30%;\n    font-weight: blue;\n}\n.nav-btn > div {\n    position: relative;\n    top: 50%;\n    transform: translateY(-50%);\n}\n.nav-btn:hover {\n    opacity: 80%;\n    font-weight: bold;\n    color: white;\n    cursor: pointer;\n}\n#carousel {\n    position: absolute;\n    top: 0px;\n    bottom: 0px;\n    left: -33%;\n    width: 166%;\n    display: flex;\n    overflow: hidden;\n}\n\n.cardcol {\n    position: relative;\n    width: 20%;\n    height: 100%;\n    margin: 0;\n    padding: 0;\n}\n.carditem {\n    position: absolute;\n    top: 10px;\n    bottom: 10px;\n    left: 0px;\n    right: 0px;\n    border-radius: 10px;\n    box-shadow: 10px 10px 10px rgba(128, 128, 128, 1);\n}\n\n@keyframes left0 {\n    from {\n        transform: translateX(0) scale(0.6);\n    }\n    to {\n        transform: translateX(-100%) scale(0.4);\n    }\n}\n@keyframes right0 {\n    from {\n        transform: translateX(0) scale(0.6);\n    }\n    to {\n        transform: translateX(100%) scale(0.8);\n    }\n}\n@keyframes left1 {\n    from {\n        transform: translateX(0) scale(0.8);\n    }\n    to {\n        transform: translateX(-100%) scale(0.6);\n    }\n}\n@keyframes right1 {\n    from {\n        transform: translateX(0) scale(0.8);\n    }\n    to {\n        transform: translateX(100%) scale(1);\n    }\n}\n\n@keyframes left2 {\n    from {\n        transform: translateX(0) scale(1);\n    }\n    to {\n        transform: translateX(-100%) scale(0.8);\n    }\n}\n@keyframes right2 {\n    from {\n        transform: translateX(0) scale(1);\n    }\n    to {\n        transform: translateX(100%) scale(0.8);\n    }\n}\n\n@keyframes left3 {\n    from {\n        transform: translateX(0) scale(0.8);\n    }\n    to {\n        transform: translateX(-100%) scale(1);\n    }\n}\n@keyframes right3 {\n    from {\n        transform: translateX(0) scale(0.8);\n    }\n    to {\n        transform: translateX(100%) scale(0.6);\n    }\n}\n@keyframes left4 {\n    from {\n        transform: translateX(0) scale(0.6);\n    }\n    to {\n        transform: translateX(-100%) scale(0.8);\n    }\n}\n@keyframes right4 {\n    from {\n        transform: translateX(0) scale(0.6);\n    }\n    to {\n        transform: translateX(100%) scale(0.4);\n    }\n}\n#card1,\n#card3 {\n    transform: scale(0.8);\n}\n#card0,\n#card4 {\n    transform: scale(0.6);\n}\n.animate-0,\n.animate-1,\n.animate-2,\n.animate-3,\n.animate-4,\n.animate-0-back,\n.animate-1-back,\n.animate-2-back,\n.animate-3-back,\n.animate-4-back {\n    animation-duration: 0.25s;\n    animation-iteration-count: 1;\n    animation-fill-mode: forwards;\n}\n.animate-0 {\n    animation-name: left0;\n}\n.animate-0-back {\n    animation-name: right0;\n}\n\n.animate-1 {\n    animation-name: left1;\n}\n.animate-1-back {\n    animation-name: right1;\n}\n.animate-2 {\n    animation-name: left2;\n}\n.animate-2-back {\n    animation-name: right2;\n}\n.animate-3 {\n    animation-name: left3;\n}\n.animate-3-back {\n    animation-name: right3;\n}\n.animate-4 {\n    animation-name: left4;\n}\n.animate-4-back {\n    animation-name: right4;\n}\n";