export default "#movie-imgs {\n    height: 100%;\n    display: block;\n    position: relative;\n    margin: auto;\n    width: 100%;\n}\n#content {\n    position: relative;\n    text-align: center;\n    display: inline-block;\n    width: 100%;\n    min-height: 200px;\n    padding: 0;\n}\n.page {\n    background: #333;\n    margin: 0;\n    padding: 0;\n    padding-top: 10px;\n    font-family: Arial, sans-serif;\n    position: absolute;\n    top: 0px;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    overflow-y: auto;\n    min-width: 600px;\n    text-align: center;\n    background-image: url(\"assets/space.jpg\");\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: conver;\n}\n.detail {\n    position: relative;\n    border-top: solid 1px gold;\n    overflow: hidden;\n}\n#movie-title {\n    text-align: center;\n    font-size: 40px;\n    color: gold;\n    border-bottom: 1px solid white;\n}\n.movie-info {\n    overflow-y: auto;\n    display: flex;\n    flex-direction: row;\n}\n.movie-info > div {\n    display: inline-block;\n    width: 50%;\n    color: gold;\n    text-align: left;\n    font-size: 20px;\n    font-weight: bold;\n    overflow: hidden;\n    position: relative;\n}\n.movie-info a {\n    color: #fafafa;\n    text-decoration: underline;\n}\n.movie-info a:hover {\n    color: black;\n    font-style: italic;\n}\n.left-info,\n.right-info {\n    vertical-align: top;\n    display: inline-block;\n}\n.movie-info-left {\n    box-sizing: border-box;\n}\n.line {\n    display: flex;\n    flex-direction: row;\n    width: 100%;\n}\n.left-info {\n    width: 150px;\n    font-weight: bold;\n}\n.right-info {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n}\n.movie-card {\n    flex: 1;\n    padding: 20px;\n    margin: 20px;\n    font-size: 18px;\n    background: gray;\n    color: #fafafa;\n    border-radius: 10px;\n    box-shadow: 5px 5px 5px rgba(128, 128, 128, 1);\n}\n";